import { http } from '@/http';
import store from '@/store';
import Dialog from "vant/es/dialog";
import "vant/es/dialog/style/less";
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { computed, defineComponent, onMounted, reactive, ref, toRefs } from 'vue';
export default defineComponent({
    name: 'CustomerLayout',
    setup() {
        const state = reactive({
            includeRoutes: [
                'CustomerHome',
                'CustomerActs',
                'CustomerMessage',
                'CustomerInfo'
            ]
        });
        const icon1 = {
            active: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-5-1.png',
            inactive: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-5-0.png'
        };
        const icon2 = {
            active: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-6-1.png',
            inactive: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-6-0.png'
        };
        const icon3 = {
            active: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-3-1.png',
            inactive: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-3-0.png'
        };
        const icon4 = {
            active: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-4-1.png',
            inactive: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-4-0.png'
        };
        const bindShopFlag = computed(() => store.state.bindShopFlag);
        console.log(bindShopFlag.value);
        const handleChangeTabbar = (name) => {
            console.log(name);
            if (!bindShopFlag.value) {
                Dialog.confirm({
                    title: '提示',
                    message: '您还没有专属零售户哦！赶快去绑定吧！',
                    closeOnClickOverlay: true,
                    confirmButtonText: '知道了'
                })
                    .then(() => {
                    // on confirm
                })
                    .catch(() => {
                    // on cancel
                });
                return false;
            }
        };
        const CustomerHome = ref('CustomerHome');
        const hasMsg = computed(() => store.state.hasMsg);
        onMounted(() => {
            if (hasMsg.value === null) {
                http
                    .get('/hbSeller/main/role', {})
                    .then((res) => {
                    if (res.code === '200') {
                        if (res.data.hasMsg) {
                            store.commit('setHasMsg', true);
                        }
                        else {
                            store.commit('setHasMsg', false);
                        }
                    }
                    else {
                        Toast.fail(res.msg);
                    }
                })
                    .catch((err) => {
                    console.log(err);
                });
            }
        });
        return {
            ...toRefs(state),
            icon1,
            icon2,
            icon3,
            icon4,
            handleChangeTabbar,
            bindShopFlag,
            CustomerHome,
            hasMsg
        };
    }
});
